import React from "react";
const List = ({
  persons,
  filtering,
  showFiltered,
  newFilter,
  handleDelete,
}) => {
  let people = persons;
  if (filtering) {
    people = showFiltered(newFilter);
  }
  if (people.length === 0) {
    return <p>There aint no persons</p>;
  } else {
    return (
      <ul>
        {people.map((pe) => (
          <ListItem
            person={pe}
            key={pe.id}
            handleDelete={() => handleDelete(pe.id)}
          />
        ))}
      </ul>
    );
  }
};

const ListItem = ({ person, handleDelete }) => {
  return (
    <li>
      {person.name} {person.number}{" "}
      <button onClick={handleDelete}>delete</button>
    </li>
  );
};
export default List;
