import React from "react";

const Form = ({ addPerson, handleChange, newName, newNumber }) => {
  return (
    <form onSubmit={addPerson}>
      <div>
        name: <input onChange={handleChange} value={newName} id="name" />
        number: <input onChange={handleChange} value={newNumber} id="number" />
      </div>
      <div>
        <button type="submit">add</button>
      </div>
    </form>
  );
};

export default Form;
