import { useState, useEffect } from "react";
import axios from "axios";
import List from "./components/List";
import Filter from "./components/Filter";
import Form from "./components/Form";
import PopUp from "./components/PopUp";
import service from "./services/service";
import "./index.css";
import { render } from "@testing-library/react";

const App = () => {
  const [persons, setPersons] = useState([]);
  const [newName, setNewName] = useState("");
  const [newNumber, setNewNumber] = useState("");
  const [newFilter, setNewFilter] = useState("");
  const [filtering, setNewFiltering] = useState(false);
  const [pop, setPop] = useState({});

  useEffect(() => {
    service
      .getAll()
      .then((res) => {
        setPersons(res.data);
      })
      .catch((error) => {
        handlePop("error en el server", "error");
      });
  }, []);

  const addPerson = (ev) => {
    ev.preventDefault();

    const newPerson = {
      name: newName,
      number: newNumber,
    };

    try {
      validacion(newPerson);
      if (verificarExistencia(newPerson)) {
        modificarNumero(newPerson);
        handlePop("num modified", "ok");
      } else {
        service.create(newPerson).then((res) => {
          setPersons(persons.concat(res.data));
          setNewName("");
          setNewNumber("");
          handlePop("Person added", "ok");
        });
      }
    } catch (error) {
      handlePop(error, "error");
    }
  };

  const validacion = (obj) => {
    if (
      persons.find(
        (p) =>
          obj.name.toUpperCase() === p.name.toUpperCase() &&
          obj.number.toUpperCase() === p.number.toUpperCase()
      )
    ) {
      throw "error: cambie el numero, o agregue nueva persona";
    }
    if (obj.name === "" || obj.number === "") {
      throw "error: campos no pueden estar vacios";
    }
  };

  const modificarNumero = (objPerson) => {
    if (window.confirm(`Desea modificar el numero de ${objPerson.name}`)) {
      let persona = persons.find(
        (p) => p.name.toUpperCase() === objPerson.name.toUpperCase()
      );
      persona = {
        ...persona,
        number: objPerson.number,
      };
      service.update(persona.id, persona).then((res) => {
        setPersons(
          persons.map((p) => {
            return p.id !== persona.id ? p : res.data;
          })
        );
        setNewName("");
        setNewNumber("");
      });
    }
  };

  const handlePop = (message, type) => {
    setPop({ message, type });
    setTimeout(() => {
      setPop(false);
    }, 2000);
  };

  const verificarExistencia = (person) => {
    if (
      persons.find((p) => person.name.toUpperCase() === p.name.toUpperCase())
    ) {
      return true;
    }
  };

  const handleChange = (ev) => {
    if (ev.target.id === "name") {
      setNewName(ev.target.value);
    } else if (ev.target.id === "number") {
      setNewNumber(ev.target.value);
    } else if (ev.target.id === "filter") {
      setNewFiltering(true);
      setNewFilter(ev.target.value);
    }
  };

  const showFiltered = (strPerson) => {
    if (
      persons.map((p) => p.name.toUpperCase().includes(strPerson.toUpperCase()))
    ) {
      const filtered = persons.filter((p) =>
        p.name.toUpperCase().includes(strPerson.toUpperCase())
      );
      return filtered;
    }
  };

  const handleDelete = (id) => {
    console.log(persons.filter((p) => p.id !== id));
    if (window.confirm("sure u wanna delete?")) {
      service
        .remove(id)
        .then((res) => {
          console.log(res);
          setPersons(persons.filter((p) => p.id !== id));
          handlePop("removed succesfully", "ok");
        })
        .catch((error) => {
          handlePop(error, "error");
        });
    }
  };

  return (
    <div>
      <h2>Phonebook</h2>
      <Filter newFilter={newFilter} handleChange={handleChange} />

      <h2>add a new</h2>
      <Form
        addPerson={addPerson}
        handleChange={handleChange}
        newName={newName}
        newNumber={newNumber}
      />
      <h2>Numbers</h2>
      <List
        persons={persons}
        filtering={filtering}
        showFiltered={showFiltered}
        newFilter={newFilter}
        handleDelete={handleDelete}
      />
      <PopUp message={pop.message} type={pop.type} />
    </div>
  );
};

export default App;
